import React, { useEffect, useState } from 'react';
//import { useLocation} from 'react-router-dom';
import { useNavigate } from 'react-router';
import { Table, Button, Form, Row, Col, } from 'react-bootstrap';
import {LinkContainer} from 'react-router-bootstrap';
//import FormContainer from '../components/FormContainer';
import { useDispatch, useSelector } from 'react-redux';
import Message from '../components/Message';
import Loader from '../components/Loader';
import {getUserDetails, getUserUpdate} from '../actions/userAction'
import { myOrderList } from '../actions/orderAction';
import { USER_UPDATE_RESET } from '../constants/userConstants';

const ProfileScreen = () => {
    const navigate = useNavigate()
    //const location = useLocation()
    const [name,setName] = useState('');
    const [email,setEmail] = useState('');
    const [password, setPassword] = useState('');
    const [confirmPassword, setConfirmPassword] = useState('');
    const [message, setMessage] = useState(null);

    //const redirect = location.search ? location.search.split('=')[1] : '/';

    const dispatch = useDispatch();

    const userDetails = useSelector((state) => state.userDetails);
    const {loading, error, user} = userDetails;
    
    const userLogin = useSelector((state) => state.userLogin);
    const {userInfo} = userLogin;
    
    const userUpdate = useSelector((state) => state.userUpdate);
    const {success} = userUpdate
    
    const orderMyList = useSelector(state => state.orderMyList);
    const {loading:loadingOrders, error: errorOrders, orders} = orderMyList;

    useEffect(() => {
        if(!userInfo){
            navigate('/login')
        } else {
            if(!user.name || !user || success){
                dispatch({type: USER_UPDATE_RESET})
                dispatch(myOrderList())
                dispatch(getUserDetails('profile'))
            } else {
                setName(user.name)
                setEmail(user.email)
            }
        }
    }, [userInfo, dispatch, navigate, user, success])

    const submitHandler = (e) => {
        e.preventDefault()
        if(password !== confirmPassword){
            setMessage('Password do not match')
        }else{
            dispatch(getUserUpdate({id: user._id, name, email, password}))

        }

    }

    return  (
            <Row>
                <Col md={3}>
                    <h2>User Profile</h2>
                    {message && <Message variant='danger'>{message}</Message>}
                    {error && <Message variant='danger'>{error}</Message>}
                    {success && <Message variant='success'>Information Updated!</Message>}
                    {loading && <Loader/>}
                    <Form onSubmit={submitHandler}>
                        <Form.Group controlId='name'>
                            <Form.Label>Name</Form.Label>
                            <Form.Control   
                                type='name' 
                                placeholder='Enter your name' 
                                value={name}
                                onChange={(e) => setName(e.target.value)}>
                            </Form.Control>
                        </Form.Group>
                        <Form.Group controlId='email'>
                            <Form.Label>Email Address</Form.Label>
                            <Form.Control   
                                type='email' 
                                placeholder='Enter email' 
                                value={email}
                                onChange={(e) => setEmail(e.target.value)}>
                            </Form.Control>
                        </Form.Group>
                        <Form.Group controlId='password'>
                            <Form.Label>Password</Form.Label>
                            <Form.Control   
                                type='password' 
                                placeholder='Enter password' 
                                value={password}
                                onChange={(e) => setPassword(e.target.value)}>
                            </Form.Control>
                        </Form.Group>
                        <Form.Group controlId='passwordConfirmation'>
                            <Form.Label>Confirm Password</Form.Label>
                            <Form.Control   
                                type='password' 
                                placeholder='Confirm password' 
                                value={confirmPassword}
                                onChange={(e) => setConfirmPassword(e.target.value)}>
                            </Form.Control>
                        </Form.Group>
                        <Button type='submit' variant='primary' className='my-3'>Update</Button>
                    </Form>
                </Col>
                <Col md={9}>
                    <h2>My Orders</h2>
                    {loadingOrders ? 
                    (<Loader /> ): errorOrders ? 
                    (<Message variant='danger'>{errorOrders}</Message>) : 
                    (
                        <Table striped bordered hover responsive className="table-sm">
                            <thead>
                                <tr>
                                    <th>ID</th>
                                    <th>DATE</th>
                                    <th>TOTAL</th>
                                    <th>PAID</th>
                                    <th>DELIVERED</th>
                                    <th></th>
                                </tr>
                            </thead>
                            <tbody>
                                {orders.map((order) => (
                                    <tr key={order._id}>
                                        <td>{order._id}</td>
                                        <td>{order.createdAt.substring(0,10)}</td>
                                        <td>{order.totalPrice}</td>
                                        <td>{order.isPaid ? order.isPaidAt.substring(0, 10) : (<i className='fas fa-times' style={{color: 'red'}}></i>)}</td>
                                        <td>{order.isDelivered ? order.deliveredAt.substring(0, 10) : (<i className='fas fa-times' style={{color: 'red'}}></i>)}</td>

                                        <td>
                                            <LinkContainer to={`/order/${order._id}`}>
                                                <Button variant='light'>Details</Button>
                                            </LinkContainer>
                                        </td>
                                    </tr>
                                ))}
                            </tbody>
                        </Table>
                    )} 
                </Col>
            </Row>
    )
}

export default ProfileScreen